"use client";

import React, { useContext, useState } from "react";
import { useRouter } from "next/navigation";
import { RegisterContext } from "./RegisterContext";
import axios from "axios";

function Step1() {
  const context = useContext(RegisterContext);
  if (!context) throw new Error("Step1 must be used within RegisterProvider");
  
  const { formData, setFormData } = context;
  const [error, setError] = useState("");
  const router = useRouter();

  const handleNext = async (e: React.FormEvent) => {
    e.preventDefault();

    // ✅ Validation for empty fields (including availability)
    if (
      !formData.studentID ||
      !formData.firstName ||
      !formData.lastName ||
      !formData.contactNumber||
      !formData.degreeProgramme ||
      !formData.level ||
      !formData.email ||
      !formData.password ||
      !formData.availability
    ) {
      setError("All fields are required");
      return;
    }

    // ✅ Student ID rule
    if (!formData.studentID.includes("C") || !formData.studentID.includes("B")) {
      setError("Student ID must include letters 'C' and 'B'");
      return;
    }
    
    // Contact number validation
    const phoneRegex = /^[0-9]{10}$/;
    if (!phoneRegex.test(formData.contactNumber)) {
      setError("Contact number must be exactly 10 digits.");
      return;
    }

    // ✅ Email rule
    const emailRegex = new RegExp(`${formData.studentID}@students\\.apiit\\.lk$`);
    if (!emailRegex.test(formData.email)) {
      setError("Email must match format: StudentID@students.apiit.lk");
      return;
    }

    // ✅ Duplicate check (backend)
    try {
      const res = await axios.post("http://localhost:5000/api/auth/check-duplicates", {
        studentID: formData.studentID,
        email: formData.email,
      });

      if (res.data.exists) {
        setError(res.data.message);
        return;
      }
    } catch (err) {
      console.error(err);
      setError("Error checking existing accounts");
      return;
    }

    setError("");
    router.push("/register/step2");
  };

  return (
    <div className="register-wrapper">
      {/* Header */}
      <div className="register-header">
        <div className="register-badge">NEXT STEP - STUDENT JOURNEY</div>
        <h1 className="register-main-title">
          Start your placement journey with confidence.
        </h1>
        <p className="register-subtitle">
          We will guide you through a few quick steps so coordinators can match the best opportunities to your strengths.
        </p>
        <p className="register-login-link">
          Already started? <a onClick={() => router.push("/")}>Return to login</a>
        </p>
      </div>

      {/* Registration Card */}
      <div className="register-card">
        {/* Progress Bar */}
        <div className="progress-container">
          <div className="step-label">STEP 1 OF 4</div>
          <div className="progress-bar-bg">
            <div className="progress-bar-fill" style={{ width: '25%' }}></div>
          </div>
        </div>

        {/* Step Navigation */}
        <div className="step-navigation">
          <div className="step-nav-item active">
            <div className="step-icon">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2" />
                <circle cx="12" cy="7" r="4" />
              </svg>
            </div>
            <span className="step-nav-label">Basic Info</span>
          </div>
          <div className="step-nav-item inactive">
            <div className="step-icon">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2" />
              </svg>
            </div>
            <span className="step-nav-label">Preferences</span>
          </div>
          <div className="step-nav-item inactive">
            <div className="step-icon">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z" />
                <polyline points="14 2 14 8 20 8" />
              </svg>
            </div>
            <span className="step-nav-label">Credentials</span>
          </div>
          <div className="step-nav-item inactive">
            <div className="step-icon">
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2" />
                <circle cx="12" cy="7" r="4" />
              </svg>
            </div>
            <span className="step-nav-label">Profile</span>
          </div>
        </div>

        {/* Form Content */}
        <div className="register-form-content">
          <h2 className="form-section-title">Basic Info</h2>
          <p className="form-section-subtitle">
            Introduce yourself and share your academic details.
          </p>

          <form onSubmit={handleNext}>
            <div className="form-grid">
              <div className="form-field full-width">
                <label htmlFor="studentID">Student ID</label>
                <input
                  id="studentID"
                  type="text"
                  placeholder="nanu@example.com"
                  value={formData.studentID || ""}
                  onChange={(e) => setFormData({ ...formData, studentID: e.target.value })}
                />
              </div>

              <div className="form-field full-width">
                <label htmlFor="degreeProgramme">Degree Programme</label>
                <select
                  id="degreeProgramme"
                  value={formData.degreeProgramme || ""}
                  onChange={(e) => setFormData({ ...formData, degreeProgramme: e.target.value })}
                >
                  <option value="">Software Engineering</option>
                  <option value="Software Engineering">Software Engineering</option>
                  <option value="Computer Science">Computer Science</option>
                  <option value="Cyber Security">Cyber Security</option>
                </select>
              </div>

              <div className="form-field">
                <label htmlFor="firstName">First Name</label>
                <input
                  id="firstName"
                  type="text"
                  placeholder="nanu"
                  value={formData.firstName || ""}
                  onChange={(e) => setFormData({ ...formData, firstName: e.target.value })}
                />
              </div>

              <div className="form-field">
                <label htmlFor="lastName">Last Name</label>
                <input
                  id="lastName"
                  type="text"
                  placeholder="nntn"
                  value={formData.lastName || ""}
                  onChange={(e) => setFormData({ ...formData, lastName: e.target.value })}
                />
              </div>

              <div className="form-field full-width">
                <label htmlFor="level">General level</label>
                <select
                  id="level"
                  value={formData.level || ""}
                  onChange={(e) => setFormData({ ...formData, level: e.target.value })}
                >
                  <option value="">Level 5</option>
                  <option value="Level 5">Level 5</option>
                  <option value="Level 6">Level 6</option>
                </select>
              </div>

              <div className="form-field full-width">
                <label>Availability</label>
                <div className="radio-group">
                  <div className="radio-option">
                    <input
                      type="radio"
                      id="fulltime"
                      name="availability"
                      value="Full-time"
                      checked={formData.availability === "Full-time"}
                      onChange={(e) => setFormData({ ...formData, availability: e.target.value })}
                    />
                    <label htmlFor="fulltime">Full-time</label>
                  </div>
                  <div className="radio-option">
                    <input
                      type="radio"
                      id="parttime"
                      name="availability"
                      value="Part-time"
                      checked={formData.availability === "Part-time"}
                      onChange={(e) => setFormData({ ...formData, availability: e.target.value })}
                    />
                    <label htmlFor="parttime">Part-time</label>
                  </div>
                </div>
              </div>

              <div className="form-field full-width">
                <label htmlFor="universityEmail">University Email</label>
                <input
                  id="universityEmail"
                  type="email"
                  placeholder="nanu@techie.com"
                  value={formData.email || ""}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                />
              </div>

              <div className="form-field full-width">
                <label htmlFor="password">Create Password</label>
                <input
                  id="password"
                  type="password"
                  placeholder="••••••••••"
                  value={formData.password || ""}
                  onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                />
              </div>
            </div>

            {error && <div className="register-error">{error}</div>}

            <div className="form-actions">
              <button type="submit" className="btn-next">
                Continue to preferences
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Step1;
